import {
  Slider,
  Radio,
  Button,
  Space,
  Card,
  Row,
  Col,
  InputNumber,
} from "antd";
import { useState } from "react";
import "./App.css";
const WAVEFORMS = ["sine", "square", "sawtooth", "triangle"];

export function Main_control_section() {
  const [waveformType, setWaveformType] = useState("sine");
  const [amplitude, setAmplitude] = useState(5);
  const [frequency, setFrequency] = useState(440);
  //play sound func
  function when_play_btn_clicked() {
    const actx = new AudioContext();
    if (!actx) throw "Not Supported";
    const osc = actx.createOscillator();
    osc.type = waveformType;
    osc.amplitude = amplitude;
    osc.frequency.value = frequency; //frequency of the key
    osc.connect(actx.destination);
    osc.start();
    osc.stop(actx.currentTime + 2);
  }

  const onChangeWaveform = (e) => {
    setWaveformType(e.target.value);
  };
  const onChangeAmplitude = (newVal) => {
    setAmplitude(newVal);
  };
  const onChangeFrequency = (newVal) => {
    setFrequency(newVal);
  };

  const options = [
    {
      label: "sine",
      value: "sine",
    },
    {
      label: "square",
      value: "square",
    },
    {
      label: "sawtooth",
      value: "sawtooth",
    },
    {
      label: "triangle",
      value: "triangle",
    },
  ];
  return (
    <Space
      direction="vertical"
      size="middle"
      align="start"
      className="SythnSection"
      style={{ minWidth: "300px" }}
    >
      <Card title="Main Control" size="medium">
        <Button type="primary" onClick={when_play_btn_clicked}>
          Play
        </Button>
      </Card>

      <Card title="Waveform" size="medium">
        <Radio.Group
          options={options}
          onChange={onChangeWaveform}
          value={waveformType}
          optionType="button"
        />
      </Card>

      <Card title="Frequency" size="medium" style={{ width: "300px" }}>
        <Row span={100}>
          <Col span={10}>
            <Slider
              min={30}
              max={3000}
              onChange={onChangeFrequency}
              value={typeof frequency === "number" ? frequency : 440}
              step={1}
            />
          </Col>
          <Col span={4}>
            <InputNumber
              min={30}
              max={3000}
              style={{
                margin: "0 16px",
              }}
              step={10}
              value={frequency}
              onChange={onChangeFrequency}
            />
          </Col>
        </Row>
      </Card>

      <Card title="Amplitude" size="medium" style={{ width: "300px" }}>
        <Row span={100}>
          <Col span={10}>
            <Slider
              min={0}
              max={10}
              onChange={onChangeAmplitude}
              value={typeof amplitude === "number" ? amplitude : 5}
              step={0.1}
            />
          </Col>
          <Col span={4}>
            <InputNumber
              min={0}
              max={10}
              style={{
                margin: "0 16px",
              }}
              step={0.1}
              value={amplitude}
              onChange={onChangeAmplitude}
            />
          </Col>
        </Row>
      </Card>
    </Space>
  );
}
